import { Injectable } from '@angular/core';
import { Http, URLSearchParams, Response, Headers, RequestOptions, RequestMethod } from '@angular/http';
import { Router } from '@angular/router';
import { Observable } from 'rxjs/Rx';
import { URL_OAUTH, URL_OAUTH_CLIENT_ID, OAUTH_CLIENT_SECRET, OAUTH_GRANT_TYPE } from '../utils/app.constants'
import { MENSAJE_ERROR_BAD_CREDENTIALS, MENSAJE_ERROR_GENERICO } from '../utils/messages.constants';

@Injectable()
export class LoginService {
    constructor(public http: Http, public router: Router) { }

  public login(username, password): Observable<any> {
    let headers = new Headers({'Content-Type': 'application/json'});
    headers.set("Authorization", "Basic " + btoa(URL_OAUTH_CLIENT_ID + ":" + OAUTH_CLIENT_SECRET));
    // let headers = new Headers({'Content-Type': 'application/json', 'Access-Control-Allow-Origin': '*' });

    let params = new URLSearchParams();
    params.append("grant_type", OAUTH_GRANT_TYPE);
    params.append("username", username);
    params.append("password", password);

    let pass = {
      "grant_type": OAUTH_GRANT_TYPE,
      "username": username,
      "password": password
    };

    let options = new RequestOptions({ headers: headers });
    // return this.http.post(`${URL_OAUTH}/token`, params, options).map(this.handleData)
    //     .catch(this.handleError);

    return this.http.post(`https://comprobantes-b1263.firebaseio.com/token.json`, pass, options).map(this.handleData)
      .catch(this.handleError);
  }

  public users(){
    let headers = new Headers({'Content-Type': 'application/json'});
    headers.set("Authorization", "Basic " + btoa(URL_OAUTH_CLIENT_ID + ":" + OAUTH_CLIENT_SECRET));
    let options = new RequestOptions({ headers: headers });

    return this.http
      .get("http://localhost:3000/users",options)
      .map(this.handleData)
      .catch(this.handleError);
  }

    private handleData(res: Response) {
        let body = res.json();
        return body;
    }

    private handleError(error: any) {

        let errMsg = error.statusText;

        if (error.status == "400") {
            errMsg = MENSAJE_ERROR_BAD_CREDENTIALS;
        }
        else {
            errMsg = MENSAJE_ERROR_GENERICO;
        }
        return Observable.throw(errMsg);
    }

    public logout() {
        sessionStorage.clear();
        this.router.navigateByUrl('/login');
    }
}



