import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { RouterModule } from '@angular/router';
import { SidebarComponent } from './sidebar.component';
import {MenuFacturacionElectronicaModule} from '../facturacion-electronica/menu-facturacion-electronica/menu-facturacion-electronica.module';

@NgModule({
    imports: [
      RouterModule,
      CommonModule ,
      MenuFacturacionElectronicaModule
    ],
    declarations: [
      SidebarComponent
    ],
    exports: [
      SidebarComponent
    ]
})

export class SidebarModule {}



