import { Injectable } from '@angular/core';
import { Http, Response, RequestOptions, Headers } from '@angular/http';
import 'rxjs/add/operator/map'
import { Observable } from 'rxjs/Observable';

import { ResponseError } from '../model/responseerror';
/*import { Configuration } from '../app.constants';*/
import 'rxjs/add/operator/catch';
import 'rxjs/add/observable/throw';

@Injectable()
export class MasterService {

  //private urlList: string = 'http://b2miningdata.com/params/v1/master/list/%s/%s';

  private urlList: string = 'http://b2miningdata.com/api/msmaestro/v1/params/?idorganizacion=%s&idtabla=%s&portal=PEB2M';
  private urlListJerarquia: string = 'http://b2miningdata.com/api/msmaestro/v1/params/?idorganizacion=%s&idtabla=%s&portal=PEB2M&idtablapadre=%s&idregistropadre=%s';



  constructor(private http: Http) {
  }

  listar(vc_org: string, vc_id_tabla: string): Observable<any> {

    let items$ = this.http
      .get(this.urlList.replace(/%s/, vc_org).replace(/%s/, vc_id_tabla), { headers: this.getHeaders() })
      .map(this.extractData)
      .catch(this.handleError);
    return items$;
  }

  listarConJerarquia(vc_org: string, vc_id_tabla: string, idtabla_padre: string, idregistro_padre: string): Observable<any> {

    let items$ = this.http
      .get(this.urlListJerarquia.replace(/%s/, vc_org).replace(/%s/, vc_id_tabla).replace(/%s/, idtabla_padre).replace(/%s/, idregistro_padre), 
      { headers: this.getHeaders() })
      .map(this.extractData)
      .catch(this.handleError);
    return items$;
  }


  private extractData(res: Response) {

    let respuesta = {
      status: res ? res.status : -1,
      statusText: res ? res.statusText : "ERROR",
      data: res ? res.json() || {} : {},
    }
    return respuesta;
    //return body.data || {};
  }
  private handleError(error: Response | any) {
    //console.error(error.message || error);
    return Observable.throw(error.message || error);
  }

  private getHeaders() {
    // I included these headers because otherwise FireFox
    // will request text/html
    let headers = new Headers();
    headers.append('Content-Type', 'application/json');
    //  headers.append('Accept', 'application/json');
    // headers.append('Access-Control-Allow-Origin', '*');

    //  let usuarioActual = JSON.parse(localStorage.getItem('usuarioActual'));
    //  headers.append('Authorization', usuarioActual.token);
    console.log(sessionStorage.getItem('token'));
    headers.append('Authorization', 'Bearer ' + sessionStorage.getItem('token'));
    return headers;
  }

}








